<template>
<!-- 面包屑	-->
	<el-card class = "box">
		<my-bread level1="用户管理" level2="用户列表"></my-bread>
       
		<el-row class = "search">
			<el-col>
		<div style="font-size:14px ;margin:10px 0;" @click="getUserList">
			<i class="el-icon-arrow-left"></i>
			<span>返回角色列表</span></div>			
   <!-- 搜索 -->				
	     <el-input placeholder="请输入内容" v-model="adminName" class="input">    
	     <el-button 
		 slot="append"
		  icon="el-icon-search"
		  @click="searchUser"
		 ></el-button>		
	     </el-input>
   <!-- 添加用户 -->
		  <el-button type="success" class="add" @click="showAddUser">添加用户</el-button>						 
			</el-col>
	  		
		
		</el-row>	
	<!-- 用户表格 -->
	    <el-table
		     v-loading="loading"
	         class="table"
	         :data="userlist"
	         style="width: 100%">
	         <el-table-column
			   type="index"
	           
	           label="#"
	           width="50">
	         </el-table-column>
	         <el-table-column
	           prop="id"
	           label="用户id"
	           width="150">
	         </el-table-column>
			 <el-table-column
			   prop="adminName"
			   label="用户名"
			   width="150">
			 </el-table-column>
	        
			 <el-table-column
			   prop="adminLimits"
			   label="权限等级">
			 </el-table-column>
			 
			 <el-table-column
			   prop="roleName"
			   label="角色名">
			 </el-table-column>
			 <!-- <el-table-column
					   
			   label="状态">
			   <template slot-scope = "scope">
			 <el-switch
			   v-model="scope.row.state"
			   active-color="#13ce66"
			   inactive-color="#ff4949">
			 </el-switch>  
			 </template>
			 </el-table-column> -->
			<el-table-column	         
	          label="操作" 
			  class="sosa">
			  <template slot-scope="scope">
				  <!-- 修改权限 -->
			<el-button size="mini" :plain="true" type="primary" 
			 icon="el-icon-edit" circle @click="showEditUserlimit(scope.row)"></el-button>
			      <!-- 修改角色名 -->
			   <el-button size="mini" :plain="true" type="primary"
			    icon="el-icon-user" circle @click="showEditUserRole(scope.row)"></el-button>
				
			<el-button size="mini"
				 :plain="true"
				  type="danger"
				  @click="showdeluser(scope.row)"
				   icon="el-icon-delete" 
				   circle></el-button>
			 </template>
			 </el-table-column>
	       </el-table>
	
	
		   
    <!-- 分页-->
	   <!-- <el-pagination
			  class = "pagination"
	        
			  @size-change="handleSizeChange"
			  @current-change="handleCurrentChange"
			  :current-page.sync="currentPage2"
			  :page-sizes="[2, 4, 6, 8]"
			   layout="total, sizes, prev, pager, next, jumper"     
	          :page-size="2"
	          :total="6"
			  >
	    </el-pagination> -->
	<!-- 对话框 -->
	<!-- 1.添加用户的对话框 -->
	
	<el-dialog title="添加用户" :visible.sync="dialogFormVisibleAdd" :modal-append-to-body="false">
	  <el-form :model="form">
	    <el-form-item label="用户名" label-width="80px">
	      <el-input v-model="form.name" autocomplete="off"></el-input>
	    </el-form-item>
	    
		<el-form-item label="密码" label-width="80px">
		  <el-input v-model="form.tel" autocomplete="off"></el-input>
		</el-form-item>
		<el-form-item label="权限等级" label-width="80px">
		  <el-input v-model="form.limit" autocomplete="off"></el-input>
		</el-form-item>
		<el-form-item label="角色名" label-width="80px">
		  <el-input v-model="form.roleName" autocomplete="off"></el-input>
		</el-form-item>
	  </el-form>
	  <div slot="footer" class="dialog-footer">
	    <el-button @click="dialogFormVisibleAdd = false">取 消</el-button>
	    <el-button type="primary" @click="addUser">确 定</el-button>
	  </div>
	</el-dialog>
			
			
	<el-dialog title="编辑用户权限" :visible.sync="dialogFormVisibleEditLimit" :modal-append-to-body="false">
	  <el-form :model="form">
	    <el-form-item label="用户id" label-width="80px">
	      <el-input v-model="form.id" autocomplete="off" disabled></el-input>
	    </el-form-item>
	    
		<el-form-item label="权限等级" label-width="80px">
		  <el-input v-model="form.limit" autocomplete="off"></el-input>
		</el-form-item>
		
	  </el-form>
	  <div slot="footer" class="dialog-footer">
	    <el-button @click="dialogFormVisibleEditLimit = false">取 消</el-button>
	    <el-button type="primary" @click="editUserlimit" >确 定</el-button>
	  </div>
	</el-dialog>
			
			
			
			
   <el-dialog title="编辑用户角色" :visible.sync="dialogFormVisibleEditRole" :modal-append-to-body="false">
      <el-form :model="form">
        <el-form-item label="用户id" label-width="80px" >
          <el-input v-model="form.id" autocomplete="off" disabled></el-input>
        </el-form-item>
  
    	
    	<el-form-item label="角色名" label-width="80px">
    	  <el-input v-model="form.roleName" autocomplete="off"></el-input>
    	</el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisibleEditRole = false">取 消</el-button>
        <el-button type="primary" @click="editUserRole" >确 定</el-button>
      </div>
    </el-dialog>	
		
		
	<el-dialog title="删除用户角色" :visible.sync="dialogFormVisibleDelete" :modal-append-to-body="false">
	   <el-form :model="form">
	     <el-form-item label="用户id" label-width="80px">
	       <el-input v-model="form.id" autocomplete="off" disabled></el-input>
	     </el-form-item>
	  
	 
	   </el-form>
	   <div slot="footer" class="dialog-footer">
	     <el-button @click="dialogFormVisibleDelete = false">取 消</el-button>
	     <el-button type="primary" @click="deleteUser" >确 定</el-button>
	   </div>
	 </el-dialog>			
		
	</el-card>	
	
</template>	
	
	
	
	


<script>
	export default {
		name:'user',
		data(){
			return{
			pagenum:2,
			pagesize:2,
			adminName:'',
			userlist:[],			
			dialogFormVisibleAdd: false,
			dialogFormVisibleEditLimit:false,
			dialogFormVisibleEditRole:false,
			dialogFormVisibleDelete:false,
		  form: {
		  id:'',
	      name: '',
          password:'',
		  limit:'',
		  roleName:''
		 
			      },
		 loading: true	
			}
		},
		created() {
			this.getUserList()
		},
		
		methods:{
		//query    查询参数   可以为空
		//pagename 当前页码   不能为空
		//pagesize 每页显示条数 不能为空
		 // handleSizeChange(val) {
		 //        console.log(`每页 ${val} 条`);
			// 	this.pagesize = val
			// 	this.pagenum = 1
			// 	this.getUserList()
		 //      },
		 // handleCurrentChange(val) {
		 //        console.log(`当前页: ${val}`);
		 //      },
		
		// 获取用户列表
		async getUserList(){		
		const res =  await this.$http.get('/admin/user/list')	
		console.log(res)		
		if(res.status==200){
		this.userlist = res.data
		this.loading = false
		}else{
		
		this.$message.success('获取失败')		
		}			
		},
		
		
		
		// 搜索用户
		async searchUser(){
		const res =  await this.$http.get(`/admin/findAdminInfo?adminName=${this.adminName}`)	
		console.log(res)		
		if(res.status==200){
	    this.userlist = res.data		
		this.loading = false
		}else {
		this.$message.success('此用户不存在')		
		}				
		},
		
		
		// 添加用户
		async addUser(){
		await this.$http.post(
		`/admin/user/add?
		adminName=${this.form.name}&adminPassword=${this.form.password}&adminLimits=${this.form.limit}&roleName=${this.form.roleName}`  )	
		this.dialogFormVisibleAdd = false
		this.getUserList()
		},
		
		async editUserlimit(){
		
		await this.$http.post(`/admin/user/updateLimits?id=${this.form.id}&adminLimits=${this.form.limit}`) 	
		this.dialogFormVisibleEditLimit = false
		this.getUserList()
		},
		
		
		async editUserRole(){
		
		await this.$http.post(`/admin/user/updateRole?id=${this.form.id}&roleName=${this.form.roleName}`) 	
		this.dialogFormVisibleEditRole = false
		this.getUserList()
		},	
		
	 //    showdeluser(){
			
			
		// 	 this.$confirm('此操作将永久删除该用户, 是否继续?', '提示', {
		// 	          confirmButtonText: '确定',
		// 	          cancelButtonText: '取消',
		// 	          type: 'warning'
		// 	        }).then(() => {
						
						
		// 	          this.$message({
		// 	            type: 'success',
		// 	            message: '删除成功!'
		// 	          });
		// 	        }).catch(() => {
		// 	          this.$message({
		// 	            type: 'info',
		// 	            message: '已取消删除'
		// 	          });          
		// 	        });
		// }
		
	  async	deleteUser(){
		await this.$http.delete(`/admin/user/delete?id=${this.form.id}`)	
		this.dialogFormVisibleDelete = false
		this.getUserList()
		},
		
		showAddUser(){
			this.form.limit = ''
			this.form.roleName = ''
		    this.dialogFormVisibleAdd = true	
		},
		
		showEditUserlimit(user){
			console.log(user)
			this.form.id = user.id
			this.form.limit = user.adminLimits
			this.dialogFormVisibleEditLimit = true
		},
		showEditUserRole(user){
			this.dialogFormVisibleEditRole = true
			this.form.id = user.id
			this.form.roleName = user.roleName
			
		},
		showdeluser(user){
			this.dialogFormVisibleDelete = true
			this.form.id = user.id
		}
	}
	}
</script>

<style scoped>
	.box{
		height: 98%;
	}
	.input {
	    width: 300px;
	  }
	 .search{
		margin-top: 25px; 
		
	 }
	 .add{
		 margin-left: 20px;
	 }
	 .table{
		 margin-top: 20px;
	 }
	.pagination{
		text-align: center;
	}
</style>
