<template>
<div class="login-wrap">
	<el-form
	 class = "login-form"
	label-position="top" 
	label-width="80px" 
	:model="formdata">
	  <h2 style="text-align: center;">用户登录</h2>
	  <el-form-item label="用户名">
	    <el-input v-model="formdata.adminName"></el-input>
	  </el-form-item>
	  <el-form-item label="密码" >
	    <el-input v-model="formdata.adminPassword" type='password'></el-input>
	  </el-form-item>
	  <el-button type="primary" class = "login-btn" @click.prevent = "handleLogin()" >登录</el-button>
	</el-form>
</div>
</template>

<script>
export default{
	name:'login',
  data(){
	return{
		formdata:{
			adminName:'',
			adminPassword:''
		}
	}
  },
  
  methods:{
	 
	async handleLogin(){
		console.log(this.formdata)
	  await this.$http.post
	  (`/admin/login?adminName=${this.formdata.adminName}&adminPassword=${this.formdata.adminPassword}` ).then(res => {
		  
	    const meta = res.data.status
		if(meta === 200){
		 
		 this.$router.push('/home')	
		this.$message.success('登录成功')
		}else{
			 this.$message.error('登录失败')
			 }
		console.log(res)
		 })
		
		
		// if(this.formdata.username === 'xuan'&&this.formdata.password === '123456'){
		//  this.$router.push('/home')	//({name:'home'})
		//  this.$message.success('登录成功！')
		         
		// }else if(this.formdata.username === ''||this.formdata.password === ''){
		//  this.$message.warning('用户名或密码不能为空！')	
		// }else{
		// this.$message.error('用户名或密码错误！')	
		// }
		
	 
    }  
  }
}
</script>

<style scoped>
.login-wrap{
	height: 100%;
	background-image: url(../../assets/img/pics-bg.jpg);
	background-size: 100% 100%;
	display: flex;
	justify-content: center;
	align-items: center;
}
.login-form{
	background-color: #f6f6f6;
	width: 350px;
	height: 380px;
	padding: 27px;
	border-radius: 6px;
}
.login-form .login-btn{
	width: 100%;
}
</style>
