<template>
	
	<el-card class = "box">
		<!-- 面包屑 -->
		<my-bread level1="权限管理" level2="角色列表"></my-bread>
		<!-- 添加角色按钮 -->
		<el-row class="addrole">
			<el-col>
			<el-button type="primary" plain >添加角色</el-button>	
				
			</el-col>
		</el-row>
		
		<!-- 角色表格 -->
		<el-table
		      v-loading="loading"
		     class="table"
		     :data="rolelist"
		     style="width: 100%">
			 
			 <el-table-column
			   type="expand"			  
			   width="50">
			 </el-table-column>
			 
		     <el-table-column
			   type="index"
		       
		       label="#"
		       width="50">
		     </el-table-column>
		     <el-table-column
		       prop="roleName"
		       label="角色名称"
		       width="120">
		     </el-table-column>
		     <el-table-column
		        prop="roleDesc"
		       label="角色描述">
		     </el-table-column>
			
			
			 <el-table-column	         
		      label="操作" 
			  class="sosa">
			  <template>
			<el-button size="mini" :plain="true" type="primary" 
			 icon="el-icon-edit" circle @click="showEditUser"></el-button>
			<el-button size="mini"
			 :plain="true"
			  type="danger"
			  @click="showdeluser"
			   icon="el-icon-delete" 
			   circle></el-button>
			 </template>
			 </el-table-column>
		   </el-table>
	</el-card>
	
</template>

<script>
	export default{
	 name:'admin',
	data(){
		return{
		rolelist:[],
		loading: true	
		}
		
	},
	created() {
		this.getrolelist()
	},
	
	 methods:{
		async getrolelist(){
		const res = await this.$http.get(`roles`)
		console.log(res)
		this.meta = res.data.meta
		if(this.meta.status === 200){
		 this.rolelist = res.data.data 
		 this.$message.success(this.meta.msg)
		 this.loading = false
		}
		 }
	 }
	}
</script>

<style scoped>
	.box{
		height: 98%
	}
	.addrole{
		margin-top: 20px;
	}
</style>
