<template>
	<el-card class = "box">
		<my-bread level1="设备端管理" level2="设备列表"></my-bread>
		
		<div style="font-size:14px ;margin:10px 0;" @click="getDevicelist">
			<i class="el-icon-arrow-left"></i>
			<span>返回角色列表</span></div>	
			
		<el-input placeholder="请输入用户码查询" v-model="usercode" class="input">
		<el-button 
		slot="append"
		 icon="el-icon-search"
		 @click="searchDevice"
		></el-button>		
		</el-input>
		<el-table
		    
		     class="table"
		     :data="devicelist"
		     style="width: 100% ;margin-top: 20px;">
		     <el-table-column
		       prop="id"	   
		       label="设备id"
			   width="100">
		       
		     </el-table-column>
		     			
		     <el-table-column
		       prop="userCode"
		       label="用户码"
		       width="200">
		     </el-table-column>
		     <el-table-column
		        prop="brand_type"
				width="200"
		       label="型号">
		     </el-table-column>
			 <el-table-column
			   prop="brand"
			   label="品牌">
			 </el-table-column>
			
			
		   </el-table>
			
	</el-card>
</template>

<script>
	export default{
		name:'equipitem',
	
		data(){
			return{
				devicelist:[],
				usercode:''
			}
			
		},
		
		created() {
			this.getDevicelist()
		},
		methods:{
			
	  async getDevicelist(){
				const res = await this.$http.get(`/collect/listDevice`)
				console.log(res)
				this.devicelist = res.data
		},
		
		
	async searchDevice(){
		const res = await this.$http.post(`/collect/findListDevice?userCode=${this.usercode}`)
		this.devicelist = res.data
	}	
	},	
	}
</script>

<style scoped>
	.box{
		height: 98%;
	}
	.input{
		width: 300px;
		margin: 20px 0;
	}
</style>
