import Vue from 'vue'
import Router from 'vue-router'
import Login from '../components/login/login.vue'
import Home from '../components/home/home.vue'
import User from '../components/user/user.vue'
import Admin from '../components/manager/admin.vue'
import Power from '../components/manager/power.vue'
import Equipitem from '../components/equipment/equipitem.vue'
import Equipaducation from '../components/equipment/equipaducation.vue'
import Collection from '../components/collection/collection.vue'
import NagativeOxy from '../components/data/nagative-oxy.vue'
import PM25Trace from '../components/data/pm25trace.vue'
import OutTem from '../components/data/out-tem.vue'
import OutHumid from '../components/data/out-humid.vue'
import RoomTem from '../components/data/room-tem.vue'
import RoomHumid from '../components/data/room-humid.vue'
Vue.use(Router)
const routes = [
	{path:'',
	redirect:'/login',           //redirect重定向
 
	},
    { name:'login',
	  path: '/login',
      component:Login,
	  meta:{                     //meta元数据（描述数据的数据）
		  title:'登录'
	  }
    },
	{
	name:'home'	,
	path: '/home',
	component:Home,
	meta:{
		title:'后台管理'
	},
	children:[
		{
	name:'user'	,
	path:'/user',
	component:User,
	},
	{
	name:'admin'	,
	path:'/admin',
	component:Admin,
	},
	{
	name:'power'	,
	path:'/power',
	component:Power,
	},
	{
	name:'equipitem'	,
	path:'/equipitem',
	component:Equipitem,
	},
	{
	name:'equipaducation'	,
	path:'/equipaducation',
	component:Equipaducation,
	},
	{
	name:'collection'	,
	path:'/collection',
	component:Collection,
	},
	{
	name:'pm25trace',
	path:'/pm25trace',
	component:PM25Trace,
	},
	{
	name:'nagative-oxy'	,
	path:'/nagative-oxy',
	component:NagativeOxy
	},
	{
	name:'out-tem',
	path:'/out-tem',
	component:OutTem,
	},
	{
	name:'out-humid',
	path:'/out-humid',
	component:OutHumid,
	},
	{
	name:'room-tem',
	path:'/room-tem',
	component:RoomTem,
	},
	{
	name:'room-humid',
	path:'/room-humid',
	component:RoomHumid,
	},
	]
	}
   
   ]
const router = new Router({
  routes,
  mode:'history',

})
export default router